import React, { useState, useEffect } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "../../scss/master.scss";

export default () => {
  return (
    <ScrollAnimation
      animateIn="fadeIn"
      duration={1.5}
      className="main-section master-section"
    >
      <div className="wrapper master-wrap"></div>
    </ScrollAnimation>
  );
};
