import React, { useState, useEffect } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "../../scss/center.scss";

export default () => {
  const [btype, setBtype] = useState(true);
  const [bNum, setBNum] = useState("");
  const regExp = /^\d+$/;

  const inputbNum = (e) => {
    const bNumber = regExp.test(e.target.value);
    const { value, maxLength } = e.target;

    if (bNumber || e.target.value === "") {
      setBNum(value.slice(0, maxLength));
    }
  };

  return (
    <ScrollAnimation
      animateIn="fadeIn"
      duration={1.2}
      className="main-section center-section"
    >
      <div className="wrapper center-wrap">
        {/* <h1 className="title">가맹점 신청</h1>
        <p className="sub-title">
          마스터페이 가맹점이 되시겠어요?
          <br />
          언제든지 신청하시면, 전문 가이드가 마스터페이 가맹점이 되실 수 있도록
          도와 드리겠습니다.
        </p>
        <form onSubmit={(e) => e.preventDefault()}>
          <div className="lookup-box">
            <p className="lookup-text">가맹점 여부 조회 후 신청하기</p>
            <div className="apply-box">
              <span className="label">구분</span>
              <div className="selection">
                <span
                  className={btype && "active"}
                  onClick={() => setBtype(true)}
                >
                  개인사업자
                </span>
                <span
                  className={!btype ? "active" : ""}
                  onClick={() => setBtype(false)}
                >
                  법인사업자
                </span>
              </div>
            </div>
            <div className="apply-box">
              <label className="label">사업자번호</label>
              <input
                type="number"
                placeholder="10자리 사업자 번호를 입력해 주세요"
                value={bNum}
                onChange={(e) => {
                  inputbNum(e);
                }}
                maxLength="10"
                required
              />
            </div>
            <button className="send">조회</button>
          </div>
          <button className="send">가맹점 신청</button>
        </form> */}
      </div>
    </ScrollAnimation>
  );
};
