import React from "react";
import lax from "lax.js";
import ScrollAnimation from "react-animate-on-scroll";
import Typed from "react-typed";
import "../../scss/master.scss";

class Main extends React.Component {
  constructor(props) {
    super(props);
    lax.setup();
    document.addEventListener(
      "scroll",
      (x) => {
        lax.update(window.scrollY);
      },
      false
    );
    lax.update(window.scrollY);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    lax.addElement(this.ref01);
    lax.addElement(this.ref01_01);
    lax.addElement(this.ref01_card);
    lax.addElement(this.ref02);
    lax.addElement(this.ref03);
  }

  render = () => {
    return (
      <>
        <ScrollAnimation
          animateIn="fadeIn"
          duration={1.2}
          animateOnce={true}
          className="main-section"
        >
          <div className="wrapper main-wrapper">
            <ScrollAnimation
              animateIn="fadeInUp"
              duration={1.2}
              animateOnce={true}
              className="h1-box"
            >
              <Typed
                strings={["쓰고, 받고,"]}
                typeSpeed={100}
                showCursor={false}
                startDelay={500}
                className="typing top"
              />

              <h1>MASTER</h1>
              <div className="h2-box">
                <h2>PAY</h2>
                <Typed
                  strings={["또 쓴다"]}
                  typeSpeed={70}
                  showCursor={false}
                  startDelay={1600}
                  className="typing bottom"
                />
              </div>
            </ScrollAnimation>
            <div className="main-pay-app">
              <div
                ref={(ref) => (this.ref01 = ref)}
                className="pay01"
                data-lax-translate-y="0 0, vh vh"
                data-lax-translate-x="0 300, vh 200"
                data-lax-scale="0 1, vh 0.7"
                data-lax-opacity="0 1, (0.7*vh) 0"
              ></div>
              <div
                ref={(ref) => (this.ref01_01 = ref)}
                className="pay01 reveal"
                data-lax-translate-y="0 0, vh vh"
                data-lax-translate-x="0 300, vh 200"
                data-lax-scale="0 1, vh 0.7"
              ></div>
              <div
                ref={(ref) => (this.ref01_card = ref)}
                className="pay01 card"
                data-lax-translate-y="0 0, vh vh"
                data-lax-translate-x="0 300, vh -220"
                data-lax-scale="0 0.9, vh 0.8"
              ></div>
              <div
                ref={(ref) => (this.ref02 = ref)}
                className="pay02"
                data-lax-translate-y="0 0, vh vh"
                data-lax-translate-x="0 290, vh 500"
                data-lax-opacity="0 1, (0.7*vh) 0"
                data-lax-scale="0 1, vh 0.8"
              ></div>
              <div
                ref={(ref) => (this.ref03 = ref)}
                className="pay03"
                data-lax-translate-y="0 0, vh vh"
                data-lax-translate-x="0 300, vh -100"
                data-lax-opacity="0 1, (0.7*vh) 0"
                data-lax-scale="0 1, vh 0.8"
              ></div>
            </div>

            <ScrollAnimation
              animateIn="fadeInUp"
              duration={1.2}
              animateOnce={true}
              className="pay-desc"
            >
              <p className="title">카드도 QR도 내 선택대로</p>
              <p className="text">
                마스터페이는 소비자의 취향에 따라 마스터페이 앱
                <strong>QR결제</strong>와, 신청을 통해 발송되는
                <strong>마스터페이 카드</strong>로 결제할 수 있습니다.
              </p>
              <p className="text">
                마스터페이 카드는 앱을 통해 카드에 사용할 금액을 충전하고,
                충전할 때마다 <strong>5%의 적립금</strong> 을 추가로 받을 수
                있는 혜택까지!
              </p>
            </ScrollAnimation>
          </div>
        </ScrollAnimation>
        <div className="main-section bottom">
          <ScrollAnimation
            animateIn="fadeIn"
            duration={1.2}
            animateOnce={true}
          ></ScrollAnimation>
        </div>
      </>
    );
  };
}

export default Main;
