import React, { useState, useEffect } from "react";
import ScrollAnimation from "react-animate-on-scroll";
import "../../scss/center.scss";

export default () => {
  return (
    <ScrollAnimation
      animateIn="fadeIn"
      duration={1.2}
      className="main-section center-section"
    >
      <div className="wrapper center-wrap">
        <h1 className="title">문의하기</h1>

        <form onSubmit={(e) => e.preventDefault()}>
          <div className="input-div">
            <label htmlFor="name" className="mt-h">
              이름
            </label>
            <input
              type="text"
              id="name"
              name="id"
              placeholder="이름을 입력해 주세요"
              required
            />
          </div>
          <div className="input-div">
            <label htmlFor="email" className="mt-h">
              이메일
            </label>
            <input
              type="email"
              id="name"
              name="email"
              placeholder="이메일을 입력해 주세요"
              required
            />
          </div>
          <div className="input-div">
            <label htmlFor="text" className="mt">
              내용
            </label>
            <textarea
              name=""
              id=""
              cols="30"
              rows="10"
              placeholder="문의내용을 입력해 주세요"
              required
            ></textarea>
          </div>
          <button className="send">보내기</button>
        </form>
      </div>
    </ScrollAnimation>
  );
};
